import fetch from '@/utils/fetch';

export default {
    /**
     * è·ååè¡¨
     */
    getData() {
        return fetch({
            url: '/list',
            method: 'get'
        });
    },
    /**
     * æ·»å 
     */
    add(data) {
        return fetch({
            url: '/add',
            data: data,
            method: 'post'
        });
    },
    /**
     * æ´æ°
     */
    update(data) {
        return fetch({
            url: '/update',
            data: data,
            method: 'post'
        });
    },
    /**
     * åé¡¹
     */
    getItem(data) {
        return fetch({
            url: '/item/' + data.id,
            method: 'get'
        });
    },
    /**
     * ç§»é¤
     */
    remove(data) {
        return fetch({
            url: '/remove/',
            data: data,
            method: 'post'
        });
    },
    /**
     * ç§»é¤
     */
    hook(data) {
        return fetch({
            url: '/hook/',
            data: data,
            method: 'post'
        });
    }
};


